import ChatWindow from '@/components/chat/ChatWindow';
import BackButton from '@/components/ui/buttons/BackButton';
import Header from '@/components/ui/containers/Header';
import { useRouter } from 'next/router';
import DeleteIcon from '@mui/icons-material/Delete';
import { IconButton } from '@mui/material';

export default function ChatPage({ params }: { params: { id: string } }) {
  const router = useRouter();
  const remove = async () => {
    if (!confirm('Delete this conversation?')) return;
    await fetch(`/api/conversations/${params.id}`, { method: 'DELETE' });
    router.push('/messages');
  };
  return (
    <div className="flex flex-col h-screen bg-white">
      <Header
        left={<BackButton smartNavigation />}
        center={<h1 className="text-lg font-semibold">Messages</h1>}
        right={
          <IconButton onClick={remove} edge="end" size="small">
            <DeleteIcon fontSize="small" />
          </IconButton>
        }
      />
      <ChatWindow conversationId={params.id} />
    </div>
  );
}
